.new{
    display: flex;

    .newcontainer{
        flex: 6;



        .top,.bottom{
            box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
            -webkit-box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
            padding: 10px;
            display: flex;

            h1{
                color : lightgray;
                font-size: 20px;
            }

            .left{
                flex: 1;
                text-align: center;

                img{
                    width: 100px;
                    height: 100px;
                    border-radius: 50%;
                    object-fit: cover;

                }
            }

            .right{
                flex: 2;

                form{
                    display: flex;
                    flex-wrap: wrap;
                    gap: 20px;
                    justify-content: space-around;

                    button{
                        width: 150px;
                        padding: 10px;
                        border: none;
                        background-color: teal;
                        color: white;
                        font-weight: bold;
                        cursor: pointer;
                        margin-top: 10px;

                        &:hover{
                            background-color: rgb(93, 187, 187);
                        }
                    }

                    .forminput{
                        width: 40%;

                        label{
                            display: flex;
                            align-items: center;
                            gap: 10px;

                            .icon{
                                cursor: pointer;
                            }
                        }

                        input{
                            width: 100%;
                            padding: 5px;
                            border: none;
                            border-bottom: 1px solid gray;
                        }
                        
                    }
                   
                }
            }
        }

    }


}