.featured{
    flex: 3;
    box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
    -webkit-box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
    padding: 10px;

    .top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: gray;
    
        .title{
            font-size: 16px;
            font-weight: 500;
        }
    }

    .bottom{
        padding: 20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 15px;

        .featuredchart{
            width: 100px;
            height: 150px;
        }

        .tite{
            font-weight: 00;
            color: gray;
        }
        
        .amount{
            font-size: 30px;
        }

        .desc{
            font-weight: 300;
            font-size: 12px;
            color: gray;
        }

        .summary{
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .item{
                text-align: center;

                .itemtitle{
                    font-size: 14px;
                    color: gray;
                }

                .itemresutl{
                    display: flex;
                    align-items: center;
                    margin-top: 10px;
                    font-size: 14px;


                    &.positive{
                        color:green
                    }
                    &.negative{
                        color:red
                    }
                }
            }
        }
    }
}
