 .dark {
    background-color: #111;
    color: rgb(156, 156, 156);

    .table{
        background-color: #111;

        .tableCell{
            color: gray;
        }
    }

    .navbar{
        color: #999;
        border-color:#333 ;


        .search{
            border-color: gray;
        }
    }

    .sidebar{
        background-color: #111;
        border-color: #333;

        .top{
            .logo{
                color: #999;
            }
        }
        hr{
            border-color:rgb(107, 104, 104)
        }
        ul{
            li{
                &:hover{
                    background-color: #333;
                }
                .icon{
                    color :#333
                }
            }
        }
    }
    .datatable{
        .datagrid{
            color: gray;
            border: none;

            .viewButton,.deleteButton,.cellwithStatus{
                color: gray;
                border: none;
            }
        }
    }
    input{
        background-color: transparent;
    }
}