.home{
    display: flex;
    .homecontainer{
        flex: 6;

        .widgets, .charts{
            display: flex;
            padding: 20px;
            gap: 20px;
        }
        .charts{
            padding: 5px 20px;
        }

        .listcontainer{
            box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
            -webkit-box-shadow: 2px 4px 10px 1px rgba(201, 201,201,0.47);
            padding: 20px;
            margin: 20px;
        }
        .listtitle{
            font-weight: 500;
            margin-bottom: 15px;
            color: gray;
        }

    }
}